@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";


.detailed-forecast {
    width: 25%;
    justify-content:center;
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    height: max-content;
    margin-right: 2vw;
    border-radius: 0.3vw;

    @include mobile-only {
        width: 100%;
        background-color: rgba(0, 5, 26, 0.863);
        z-index:99;
        box-shadow: 0 -3px 2px rgba(0, 32, 177, 0.856);
        position: -webkit-sticky;
        position: sticky;
        top: 0px;
        left: 0px;
        right:0px;
    }
    &__data-container {

        margin-right: 2vw;
        display:flex;
        flex-direction:row;
        align-items: center;
        justify-content:center;

        border-radius: 0.1vw;
        overflow:hidden;
        font-size:0.9vw;
        color:rgb(0, 114, 167);
        
        @include mobile-only {
            margin:0;
            font-size: 2vh;
            width: 100%;
            box-shadow:none;
            border-bottom: 1px solid rgb(255, 255, 255);
            color:rgb(235, 249, 255);
        }
    }
    &__time {

        font-size: 1.1vw;
        padding: 1vw 0;
        width: 20vw;
        @include mobile-only {
            font-size: 2vh;
            width: 60vw;
        }
    }
    &__data {
        width: 50vw;

        text-align:left;
        padding: 0.3vw;
        font-weight: bold;
        color: rgb(3, 123, 236);

        white-space:nowrap;
        @include mobile-only {
            text-shadow: 0 0 2px rgb(0, 0, 0);
        }


        
    }
    &__data-label {
        width: 50vw;
        text-align:right;
        height: 100%;
        padding: 0.5vw;
        border-right: 1px solid orangered;
        color:rgb(0, 114, 167);
        box-sizing:border-box;
        @include mobile-only {
            background-color: transparent;
            color:white;
        }

    }
}