@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.chart-temp {
    width: 60%;
    font-size: 1.3vw;
    @include mobile-only {
        width:100%;
        font-size:1.5vh;
    }
}

.chart-wind {
    width: 40%;
    font-size: 1.3vw;
    @include mobile-only {
        width:100%;
        font-size:1.5vh;
    }
    &__label {
        background-color:black;
        color:white;
        font-size: 2vh;
        margin-bottom: 2vw;

    }
}

.charts-container {
    display:flex;
    flex-direction:row;
    justify-content:space-between;
    align-items:center;
    flex-wrap:nowrap;
    @include mobile-only {
        flex-direction:column;

    }
}