@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.forecast {
    &__root-container {
        background-color:transparent;
        display:flex;
        flex-direction:row;
        flex-wrap:nowrap;
        align-items:center;
        background:rgba(218, 218, 218, 0.25);
        justify-content: space-between;
        @include mobile-only {
            flex-direction:column-reverse;
            flex-wrap:wrap;
        }
    }
    &__cards-container {
        display: flex;
        flex-direction:row;
        align-items:center;

    }
    &__card {
        display:flex;
        flex-basis: 23%;
        flex-direction: row;
        box-sizing: border-box;
        overflow:hidden;
        color:rgb(0, 114, 167);
        border-radius: 0.1vw;
        background:white;
        box-shadow: 2px 2px 2px rgb(102, 102, 102);
        @include mobile-only {
            flex-basis: 45%;
            width:48%;
            height: auto;
            font-size: 2vh;
            flex-direction:column;
            flex-wrap:nowrap;
            box-shadow:unset;
            overflow:hidden;
            border-radius: 1vh;
            margin-bottom: 1vh;
            border-bottom:2px solid rgb(0, 67, 143);
            background: rgb(0,213,244);
            background: linear-gradient(139deg, rgba(0,213,244,0.33655469023546913) 0%, rgba(169,202,255,0.6390756986388305) 18%, rgba(255,255,255,0.6390756986388305) 47%);
            &:nth-child(even) {
                background: rgb(0,213,244);
                background: linear-gradient(207deg, rgba(0,213,244,0.33655469023546913) 0%, rgba(169,202,255,0.6390756986388305) 18%, rgba(255,255,255,0.6390756986388305) 47%);
            }
        }

        &__data-container {
            width: 60%;
            font-size:0.9vw;
            display:flex;
            flex-direction: column;
            justify-content: flex-start;
            font-weight:bold;
            @include mobile-only {
                flex-direction:row;
                width: 100%;
                flex-wrap:wrap;
            }
        }

        &--current {
            width: 100%;
            color:white;
            background-color: transparent;
            overflow:hidden;
            height: max-content;
            flex-basis: 50%;
            box-shadow:none;
            
            @include mobile-only {
                flex-basis: 98%;
                width:100%;
                height: auto;
                margin:0; 
                background:unset;
                align-items:center;

            }

        }
        &--selected {
            background: rgb(253,160,45);
            background: linear-gradient(32deg, rgba(253,160,45,1) 0%, rgba(255,255,255,1) 83%);
            &:nth-child(even) {
                background: rgb(253,160,45);
            background: linear-gradient(32deg, rgba(253,160,45,1) 0%, rgba(255,255,255,1) 83%);
            }
        }
        &__city {
            font-size: 2vw;
            text-align: center;
            width: 50%;
            display:flex;
            align-items:center;
            @include mobile-only {
                font-size: 2.5vh;
                width:max-content;
                margin-bottom:-5vh;

            }
        }
        &__parameters-container {
            display:flex;
            flex-direction:row;
            justify-content: space-evenly;
            & > * {
                font-size: 1vw;
            }
            @include mobile-only {
                justify-content: space-around;
            }
        }
        
        &__time-weather {
            display:flex;
            flex-direction:column;
            align-items:center;
            flex-wrap:nowrap;
            justify-content: flex-start;
            width: 40%;
            margin-right: 2%;
            background:rgba(2, 63, 119, 0.64);            
            @include mobile-only {
                height: auto;
                align-items:flex-start;
                width:100%;
                border:none;
                flex-direction:row;
                justify-content:space-between;

                padding: 0%;

            }
            &__time {

                color:white;
                text-align:left;
                /*background-color:rgba(0, 0, 0, 0.603);*/
                text-align:center;
                font-size: 2vw;
                @include mobile-only {
                    font-size: 4vh;
                    margin-right:-5%;
                    z-index: 0;
                    text-align:left;
                    padding: 0;
                    height: auto;
                    box-sizing:border-box;
                    width: 30%;
                    color: rgb(255, 255, 255);
                    text-shadow:1px 1px 1px rgb(0, 0, 0)
                }

            }
            &__error {
                font-size: 2vw;
                margin-right: 0;
                color:white;
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right:-5%;
                    z-index: 99;
                    color:rgb(255, 255, 255);
                    text-align:center;
                    padding: 0;
                    height: auto;
                    box-sizing:border-box;
                    text-shadow:2px 2px 2px rgb(148, 67, 1);
                    background-color: rgba(0, 0, 0, 0.603);
                    padding: 1vh;

                }

            }

            
            &__icon {
                font-size: 3.0vw;
                color:rgba(223, 223, 223, 0.192);
                margin-bottom:1vh;
                z-index:1;
                width:80%;

                @include mobile-only {
                    font-size: 5vh; 
                    text-align:right;
                    width: 40%;
                    padding-right:5%;

                }
                &--Rain {
                    background: linear-gradient(to bottom, rgb(255, 255, 255), rgb(167, 216, 255));
                    color: transparent;
                    background-clip: text;  
                }
                &--Clouds {

                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(154, 168, 179));
                    color: transparent;
                    background-clip: text;  
                }
                &--Mist {
                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(57, 79, 97));
                    color: transparent;
                    background-clip: text;  
                }
                &--Clear {
                    background: linear-gradient(120deg, rgb(251, 255, 0), rgb(255, 196, 0));
                    color: transparent;
                    background-clip: text;  
                }
                &--Thunderstorm {
                    color: rgb(119, 84, 76);
                }
                &--Snow {
                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(180, 231, 255));
                    color: transparent;
                    background-clip: text;  
                }
                &--big {
                    font-size: 3vw;


                    @include mobile-only {
                        font-size: 7vh;
                        height: auto;
                        margin-top:0;
                        text-align:right;
                        
                    }
                }
            }
        }
        &__temp {
            display:flex;
            align-items:center;
            width:100%;
            height:33%;



            @include mobile-only {
                width: 50%;
                flex-direction:column;
            }
            &__icon {

                width: 20%;
                height: 100%;
                color:red;  
                margin-right: 0.5vw; 
                text-align:center;
                height: 100%;
                display:flex;
                flex-direction:row;
                align-items:center;
                justify-content:center;
                font-size: 1.7vw;
                @include mobile-only {
                    font-size: 2.5vh;



                }
            }
            &__value {

                width:70%;
                text-align:left;
                @include mobile-only {
                    font-size: 1.7vh;
                    text-align:center;
                }
                &:after {
                    content: "Â°C";
                    margin-left: 0.2vw;

                }
            }
        }
        &__wind {
            display:flex;
            align-items:center;
            width:100%;
            height:50%;
  
            @include mobile-only {
                width: 50%;
                flex-direction:column;

            }
            &__icon {
                width:20%;
                color:rgb(0, 174, 255);  
                margin-right: 0.5vw; 
                text-align:center;

                height: 100%;
                display:flex;
                flex-direction:row;
                align-items:center;
                justify-content:center;
                font-size: 1.5vw;
                @include mobile-only {
                    font-size: 2.5vh;

                }
            }
            &__value {

                width:70%;
                text-align:left;
                @include mobile-only {
                    font-size: 1.7vh;
                    width:100%;
                    text-align:center;
                }
                &:after {
                    content: "m/s";
                    margin-left: 0.2vw;
                    
                }
            }
        }
        &__wind-direction {
            display:flex;
            align-items:center;
            width:100%;
            height:50%;
            box-sizing:border-box;
            
            @include mobile-only {
                width: 100%;
                flex-direction:row;
                justify-content: flex-end;
            }
            &__icon {

                height: 100%;
                width: 20%;
                color:rgb(0, 255, 191);
                text-align:center;
                display:flex;
                flex-direction:row;
                align-items:center;
                justify-content:center;
                margin-right: 0.5vw; 
                font-size: 1.5vw;

                @include mobile-only {
                    font-size: 2.5vh;
                    display:none;

                }
            }
            &__value {
                text-align:left; 
                max-width: 70%;
                font-size: 0.8vw;
                @include mobile-only {
                    font-size: 1.7vh;

                    text-align:center;
                    max-width:100%;

                }
                &--header {
                    @include mobile-only {
                        max-width: 100%;
                        width: 40%;
                    }

                }
            }
        }

    }
    &__day-container {
        display:flex;
        align-items:center;
        justify-content:center;
        max-width: 100%;
        margin: 0;
        & > * {
            justify-content:flex-start;
        }
        &__date {
            font-size: 1.5vw;
            width: 10vw;
        }

    }
    &__day-forecasts {
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        width:60vw;
        /*padding: 0 10%;*/
        justify-content:space-around;
        align-items:center;
        overflow:hidden;
        height: 50vh;
        padding-left: 5vw;
        @include mobile-only {
            justify-content:space-evenly;
            width:100%;
            height: max-content;
            padding-left: 0vw;
        }

    }
    
}