@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.day-selector {
    display: flex;
    position:absolute;
    left:0;
    top:7vw;
    width:4vw;
    overflow:hidden;
    flex-direction:column;
    flex-wrap:nowrap;
    justify-content: space-around;
    font-size: 1.2vw;
    box-sizing:border-box;
    @include mobile-only {
        flex-direction: row;
        flex-wrap:nowrap;
        justify-content: space-around;
        position: -webkit-sticky;
        position: sticky;
        bottom: 0px;
        left: 0px;
        right:0px;
        width:100%;
        height:max-content;
        margin-top:1vh;
    }

    &__day {
        display:flex;
        flex-direction:column;
        color: white;
        width:100%;
        text-align:center;
        box-sizing:border-box;
        flex-wrap:nowrap;
        align-items:center;
        background:white;
        @include mobile-only {

        font-size: 2vh;
        margin-bottom: 0vh;
        flex-wrap:nowrap;
        justify-items:space-between;
        padding: 0;
        overflow:hidden;
        margin-bottom: 0.5vh;
        border-radius:0;
        width:20%;
        }
        &:hover {
            transform: scale(1.1);
            @include mobile-only {
                transform: none;
            }
        }
      &__of-week {
          background-color:white;
          color: white;
          width: 100%;
          height: min-content;
          font-weight:bold;
          font-size: 1vw;
          background-color: #484848;
          text-align:center;
          box-shadow: 2px 2px 2px white;
          @include mobile-only {
              font-size: 2vh;
              border:none;
              height:3.5vh;
              display:flex;
              flex-direction:row;
              align-items:center;
              justify-content:center;
              border-radius:0;

          }
          &--current {
            background-color:rgb(2, 81, 112);
            color: white;
            @include mobile-only {
                margin-bottom: 0;

                border:none;
            }

        }
      }
      &__of-month {
          width:100%;
          height:2.5vw;
          background-color: rgb(0, 153, 255);

          font-size:1.5vw;
          display:flex;
          flex-direction:row;
          align-items:center;
          justify-content:center;


          box-shadow: 2px 2px 2px white;
          @include mobile-only {
            font-size: 2.5vh;
            width:100%;
            height:3.5vh;
            border-radius: 0;
            box-shadow: none;
        }
          &--current {
            background-color:rgb(255, 187, 0);

            @include mobile-only {
                margin-bottom: 0;
                border:none;
            }

        }
      }
      
    }
}